import * as React from "react";
import { Toast } from 'antd-mobile'
import qs from 'qs'
import { getafterReport } from '../../api/consumer'



import './consumer.less'
interface ISprops {
    [propsName: string]: any
}
interface IState {
}

export default class AfterCourse extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
        }
    }
    getAfterCourse() {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        let params = {
            courseId: data.course_id,
            memberId: data.member_id
        }
        getafterReport(params).then(res => {
            if(!res.status) {
                window.location.href = res.data;
                // this.props.history.go(-1)
            }else {
                Toast.fail(res["message"],2)
                this.props.history.go(-1)
            }
        })
    }
    componentWillMount() {
        document.title = '课后报告';
        this.getAfterCourse()
    }
    
    render() {
        return (
            <div>
            </div>
        )
    }

}
